/* Variables */
@import "scss/01-variables";

html {
  font-size: 16px;
  font-size: 62.5%;
  line-height: 1.5;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  color: #000;
  font-family: "Montserrat", Helvetica, Arial, sans-serif;
  font-size: 16px;
  -webkit-overflow-scrolling: touch;
  overflow-x: hidden;
  touch-action: auto;
}

body,
input,
textarea,
button,
select {
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
}

a:focus {
  color: inherit;
}

/* Typography */
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Montserrat", Helvetica, Arial, sans-serif;
  font-weight: 600;
  letter-spacing: 0.025rem;
  line-height: 1.1;
  margin-bottom: 1.4rem;
  margin-top: 0;
  text-transform: uppercase;
}

/* Match font styling of a heading keeping semantic HTML heading structure. */
.h1 {
  font-size: 2.5rem;
}
.h2 {
  font-size: 2rem;
}
.h3 {
  font-size: 1.75rem;
}
.h4 {
  font-size: 1.5rem;
}
.h5 {
  font-size: 1.25rem;
}

/* Elements */
button,
.MuiButton-contained {
  background: $gs-blue;
  color: white;
  text-transform: uppercase;
  font-size: 1rem;
  border-radius: 2px;
  box-shadow: none;
}

/* MUI Component extensions */
.modal {
  &__content {
    padding: 1em;
    position: relative;
    background: white;
    padding: 1em;
    padding-top: 1.5em;
    border: 4px solid #53565a;
    &:after {
      content: "";
      display: block;
      height: 16px;
      background-size: 40px 40px;
      background-image: linear-gradient(
        45deg,
        #ffffff 28.57%,
        #f5f5f5 28.57%,
        #f5f5f5 50%,
        #ffffff 50%,
        #ffffff 78.57%,
        #f5f5f5 78.57%,
        #f5f5f5 100%
      );
      background-size: 50px 50px;
      width: 100%;
      top: 0;
      left: 0;
      position: absolute;
    }
  }
  /* Close icon */
  &-close.MuiSvgIcon-root {
    position: absolute;
    top: $space/2;
    right: $space/2;
    font-size: 32px;
    cursor: pointer;
  }
  &:focus {
    outline: 0;
  }
}

.MuiSwitch-colorSecondary.Mui-checked:hover {
  background-color: rgba(0, 0, 0, 0.08);
}

.message-area {
  padding: 1em;
  flex-wrap: wrap;
  width: 500px;
  max-width: 90vw;
  &__title {
    width: 100%;
    background: black;
    color: white;
  }
}

/* Utility classes */
.u {
  &-visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    border: 0;
  }
}
